.device-container {
  display: inline-block;
  position: relative;
}
.device-container .device-screen-bg {
  position: absolute;
  z-index: 0;
  background-color: #fff;
}
.device-container .ad-container {
  position: absolute;
  z-index: 1;
}
.device-container .social-mockup {
  width: 101%;
  margin-left: -1%;
}
.device-container .frame-img {
  position: relative;
  pointer-events: none;
  z-index: 2;
  /* We render the not-in-use frame images with height:0
		 * so that the image files load preemptively - but <img>
		 * elements are inline-block and will occupy an extra line
		 * of vertical space even with height: 0. So we make them
		 * display: block, so they occupy only as much vertical
		 * space as their height.
		 */
  display: block;
}
.device-container.landscape {
  width: 66%;
}
@media (max-width: 767px) {
  .device-container.landscape {
    width: 100%;
  }
}
.device-container.landscape .device-screen-bg {
  left: 3%;
  top: 6%;
  width: 94%;
  height: 87%;
}
.device-container.landscape .ad-container {
  left: 7.1%;
  top: 6.8%;
  width: 85.4%;
}
.device-container.landscape .frame-img {
  width: 100%;
}
.device-container.desktop {
  width: 100%;
}
.device-container.desktop .device-screen-bg {
  display: none;
}
.device-container.desktop .ad-container {
  width: 64%;
  left: 19%;
  top: 18%;
}
.device-container.desktop .frame-img {
  width: 100%;
  z-index: 0;
}
.device-container.portrait {
  height: 80vh;
}
.device-container.portrait .device-screen-bg {
  left: 6%;
  top: 3%;
  width: 88%;
  height: 94%;
  border-radius: 5vh;
}
.device-container.portrait .ad-container {
  left: 6.7%;
  top: 7%;
  width: 86.6%;
}
.device-container.portrait .frame-img {
  height: 100%;
}
.device-container.social .device-screen-bg {
  background-color: #000;
}
.device-container.social .no-video-alert,
.device-container.social .social-adblocker-alert {
  margin-top: 1em;
}
.device-container .device-shadow {
  position: absolute;
  width: 95%;
  left: 2.5%;
  height: 1em;
  bottom: 2em;
  box-shadow: 0 2em 1em rgba(0, 0, 0, 0.6);
  border-radius: 3em;
}
.format-picker .picker-button {
  margin: 1em 1em;
  font-size: 1.2em;
}
@media (max-width: 400px) {
  .format-picker.row {
    margin: 0!important;
  }
}
.device-picker .flex-row {
  display: flex;
