/** Charity carousel animations */
@keyframes logoFade2a {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes logoFade2b {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes logoFade3a {
  0% {
    opacity: 1;
  }
  33.3% {
    opacity: 0;
  }
  66.6% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes logoFade3b {
  0% {
    opacity: 0;
  }
  33.3% {
    opacity: 1;
  }
  66.6% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@keyframes logoFade3c {
  0% {
    opacity: 0;
  }
  33.3% {
    opacity: 0;
  }
  66.6% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.charity-carousel .charity-logo {
  animation-iteration-count: infinite;
}
.charity-carousel.count2 .charity-logo {
  animation-duration: 6s;
}
.charity-carousel.count2 :nth-child(1) {
  animation-name: logoFade2a;
}
.charity-carousel.count2 :nth-child(2) {
  animation-name: logoFade2b;
}
.charity-carousel.count3 .charity-logo {
  animation-duration: 9s;
}
.charity-carousel.count3 :nth-child(1) {
  animation-name: logoFade3a;
}
.charity-carousel.count3 :nth-child(2) {
  animation-name: logoFade3b;
}
.charity-carousel.count3 :nth-child(3) {
  animation-name: logoFade3c;
}
/** Fade- or slide-in animations for fake-feed elements */
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slide-in {
  0% {
    left: 100%;
  }
  100% {
    left: 0;
  }
}
.fake-feed {
